<template>
    <aside 
    :class="{'left-menu-hidden': $store.state.isLeftMenuHidden}"
    class="sidebar">
        <div
        @click="menuSwitcher()"
        class="aside-menu-switch">
            <span class="iconify" data-icon="fluent:line-horizontal-3-20-filled" data-inline="false"></span>
        </div>   
        <router-link v-if="!$store.state.isLeftMenuHidden" to="/" title="Enki"><h1>Enki</h1></router-link>
        <router-link v-else to="/"><h1>E</h1></router-link>
        <LeftMenu />
    </aside>
</template>

