.sidebar{
    position: fixed;
    display: flex;
    width: $sidebar_width;
    height: 100vh;
    padding-top: 100px;
    background: $bg-color-white-aside;
    flex-direction: column;
    @include transition();
    @media screen and (max-width: 870px) {
        width: $sidebar-width;
        h1{
            text-align: left;
        }
    }
    h1{
        width: 100%;
        font-size: 3rem;
        text-align: center;
        align-self: start;
        color: $bg-color-blue;
        transition: all .2s ease-out;
    }
    a{
        text-decoration: none;
        color: inherit;
    }
}
.left-menu-hidden{
    width: $sidebar-minimized;
    @media screen and (max-width: 870px) {
        width: $sidebar-hidden;
    }
    h1{
        padding-left: 25px;
        text-align: left;
    }
}
.aside-menu-switch{
    position: absolute;
    top: 22.5px;
    left: 22.5px;
    font-size: 35px;
    line-height: 0;
